@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}
.heading {
  color: #707070;
  margin-left: 25px;
  margin-bottom: 5px;
}
.city {
  background-color: #f6f1f4;
  display: inline-block;
  padding: 10px 25px;
  border-radius: 10px;
  margin: 20px 15px;
  color: #707070;
}
.container {
  display: flex;
  justify-content: space-around;
}
.weather {
  height: 300px;
  width: 55%;
  border-radius: 20px;
  background-image: linear-gradient(to right, #6e3f98, #a37697);
}
.details {
  height: 300px;
  width: 40%;
  border-radius: 20px;
  background-image: linear-gradient(to left, #6e3f98, #a37697);
}
.content-inner {
  color: white;
  width: 50%;
}
.right-side {
  text-align: right;
}
.weather .right-side {
  margin-top: 30px;
}
.left-side {
  text-align: left;
}
.container-inner {
  padding: 30px;
  display: flex;
}
.primary {
  font-size: 20px;
  font-weight: 900;
}
.secondary {
  font-size: 17px;
}
.temp {
  font-weight: 900;
  font-size: 90px;
}

/* Media Query Starts Here */
@media only screen and (max-width: 768px) {
  body {
    text-align: center;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
  .weather {
    width: 85%;
    margin: 30px 0px;
  }
  .details {
    width: 85%;
    margin: 30px 0px;
  }
  .container-inner {
    padding: 20px;
    display: flex;
  }
}
@media only screen and (max-width: 500px) {
  .city {
    padding: 10px 15px;
    margin: 10px 5px;
  }
  .container-inner {
    padding: 15px;
    display: flex;
  }
  .weather {
    width: 90%;
    margin: 20px 0px;
  }
  .details {
    width: 90%;
    margin: 20px 0px;
  }
}

